import React from 'react';
import { Table, Tabs, Modal, Form, Select, Input } from 'antd';
import usersService from '../../components/users/UsersService';
import { SearchOutlined } from '@ant-design/icons';
import '../../style/users.less';
import { Button } from 'antd';

const { Option } = Select;

class PageTable extends React.Component {
  state = {
    tabIndex: "1",
    userId: 0,
    selectObj: {},
    selectObjError: {},
    pwdObj: {
      password: "",
      confirmPassword: ""
    },
    pwdObjError: {},
    users: [],
    page: 1,
    pageSize: 10,
    totalUsers: 0,
    loading: true,
    sortOrder: "Fname",
    searchString: "",

    isDisableOpen: false,
    isResetOpen: false,
    isDeleteOpen: false,
    isEditOpen: false,
    isChangePwdOpen: false,
  }
  pwdFormRef = React.createRef();
  componentDidMount() {
    this.populateUserData();
  }
  async populateUserData() {
    try {
      this.setState({ loading: true });
      let data = {};
      if (this.state.tabIndex === "1") {
        data = await usersService.getUsers(this.state.page, this.state.pageSize, this.state.sortOrder, this.state.searchString.trim());
      } else if (this.state.tabIndex === "2") {
        data = await usersService.getAuditUsers(this.state.page, this.state.pageSize, this.state.sortOrder, this.state.searchString.trim());
      }
      this.setState({ totalUsers: data.totalUsers, users: data.users, loading: false });
    }
    catch (error) {
      // Handle error
    }
  }
  tabChange(e) {
    this.setState({ tabIndex: e, page: 1 }, () => {
      this.populateUserData();
    })
  }
  changeSearch(e) {
    this.setState({ searchString: e.target.value })
  }
  enterToSearch(e) {
    if (e.keyCode === 13) {
      this.populateUserData();
    }
  }

  async handleDisable() {
    await usersService.DisabledUser(this.state.userId);
    this.populateUserData();
    this.setState({ isDisableOpen: false });
  }
  async handleReview() {
    await usersService.ApproveUser(this.state.userId);
    this.populateUserData();
    this.setState({ isResetOpen: false });
  }
  async handleDelete() {
    await usersService.deleteUser(this.state.userId);
    this.populateUserData();
    this.setState({ isDeleteOpen: false });
  }
  editUser(e, key) {
    const { selectObj, selectObjError } = this.state;
    selectObj[key] = e;
    selectObjError[key] = !e?.trim();
    this.setState({ selectObj, selectObjError });
  }
  async handleEdit() {
    // console.log(this.state.selectObj);
    const { selectObj, selectObjError } = this.state;
    let errNum = 0;
    // 数据验证
    if (!selectObj.firstName?.trim()) {
      selectObjError.firstName = true;
      errNum++;      
    }
    if (!selectObj.lastName?.trim()) {
      selectObjError.lastName = true;
      errNum++;
    }
    if (!selectObj.email?.trim()) {
      selectObjError.email = true;
      errNum++;
    }
    if (!selectObj.role?.trim()) {
      selectObjError.role = true;
      errNum++;
    }
    if (errNum > 0) {
      this.setState({ selectObjError });
      return;
    }

    // 保存
    await usersService.updateUser(this.state.userId, this.state.selectObj);
    this.populateUserData();
    this.setState({ isEditOpen: false, selectObj: {}, selectObjError: {} });

  }

  changePwd(e, key) {
    const { pwdObj, pwdObjError } = this.state;
    pwdObj[key] = e;
    pwdObjError[key] = !e;
    pwdObjError.confirmPasswordDiff = !!pwdObj.password && !!pwdObj.confirmPassword && (pwdObj.password !== pwdObj.confirmPassword);

    this.setState({ pwdObj, pwdObjError });
  }
  async handleChangePwd() {
    const { pwdObj, pwdObjError } = this.state;
    let errNum = 0;
    // 数据验证
    if (!pwdObj.password) {
      pwdObjError.password = true;
      errNum++;
    }
    if (!pwdObj.confirmPassword) {
      pwdObjError.confirmPassword = true;
      errNum++;
    }
    if (pwdObj.password !== pwdObj.confirmPassword) {
      pwdObjError.confirmPasswordDiff = true;
      errNum++;
    }
    this.setState({ pwdObjError });
    if (errNum > 0) {
      return;
    }

    // 保存
    await usersService.changeUserPassword(this.state.userId, pwdObj);
    this.populateUserData();
    this.setState({ isChangePwdOpen: false, pwdObj: { password: "", confirmPassword: "" }, pwdObjError: {} });
  }

  render() {
    const { t } = this.props;
    const { users, selectObj, selectObjError, pwdObj, pwdObjError } = this.state;
    const items = [
      {
        key: '1',
        label: t(`users.All users`),
      },
      {
        key: '2',
        label: t(`users.Pending review`),
      },
    ];
    const columns = [
      {
        title: t('users.email'),
        dataIndex: 'email',
        sorter: true,
        sortOrder: this.state.sortOrder === 'Email' ? 'ascend' : this.state.sortOrder === 'Email_desc' ? 'descend' : false,
      },
      {
        title: t('users.firstName'),
        dataIndex: 'firstName',
        sorter: true,
        sortOrder: this.state.sortOrder === 'Fname' ? 'ascend' : this.state.sortOrder === 'Fname_desc' ? 'descend' : false,
      },
      {
        title: t('users.lastName'),
        dataIndex: 'lastName',
        sorter: true,
        sortOrder: this.state.sortOrder === 'Lname' ? 'ascend' : this.state.sortOrder === 'Lname_desc' ? 'descend' : false,
      },
      {
        title: t('users.approved'),
        dataIndex: 'approved',
        render: (text, record) => {
          return text ? t('users.reviewed') : t('users.pending review')
        }
      },
      {
        title: t('users.operate'),
        dataIndex: 'approved',
        render: (text, record) => {
          return (
            <div>
              {text ?
                <Button type="link" onClick={() => { this.setState({ userId: record.id, selectObj: record, isDisableOpen: true }) }}>{t('users.disable account')}</Button> :
                <Button type="link" onClick={() => { this.setState({ userId: record.id, selectObj: record, isResetOpen: true }) }}>{t('users.review account')}</Button>
              }
              <Button type="link" onClick={() => { this.setState({ userId: record.id, selectObj: record, isDeleteOpen: true }) }}>{t('users.delete')}</Button>
              <Button type="link" onClick={() => { this.setState({ userId: record.id, selectObj: record, isEditOpen: true }) }}>{t('users.edit')}</Button>
              <Button type="link" onClick={() => { this.setState({ userId: record.id, selectObj: record, isChangePwdOpen: true }) }}>{t('users.Change password')}</Button>
            </div>
          )
        }
      }
    ]
    return (
      <div className='users_list'>
        <Tabs className='pl_tab' defaultActiveKey="1" items={items} onChange={(e) => { this.tabChange(e) }} />
        <Input
          value={this.state.searchString}
          // size='large'
          className='search'
          placeholder={t('users.search')}
          prefix={<SearchOutlined />}
          onChange={(e) => { this.changeSearch(e) }}
          onKeyDown={(e) => { this.enterToSearch(e) }}
        />
        <Table
          columns={columns}
          dataSource={users}
          onChange={
            (pagination, filters, sorter) => {
              console.log(sorter)
              this.setState({
                // sortOrder: sorter.field,
              }, () => {
                // this.populateUserData()
              })
            }
          }
          pagination={{
            current: this.state.page,
            total: this.state.totalUsers,
            pageSize: this.state.pageSize,
            onChange: (page, pageSize) => {
              this.setState({
                page,
                pageSize
              }, () => {
                this.populateUserData()
              })
            },
            // showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`,
          }}
          rowKey={(row) => row.id}
          loading={this.state.loading}
        />
        <Modal title={t('users.Disable user')} open={this.state.isDisableOpen} onOk={() => { this.handleDisable() }} onCancel={() => { this.setState({ isDisableOpen: false }) }}>
          <p>{t('users.Are you sure you want to Disabled user?')}</p>
        </Modal>
        <Modal title={t('users.Review user')} open={this.state.isResetOpen} onOk={() => { this.handleReview() }} onCancel={() => { this.setState({ isResetOpen: false }) }}>
          <p>{t('users.Are you sure you want to Review user?')}</p>
        </Modal>
        <Modal title={t('users.Delete user')} open={this.state.isDeleteOpen} onOk={() => { this.handleDelete() }} onCancel={() => { this.setState({ isDeleteOpen: false }) }}>
          <p>{t('users.Are you sure you want to Delete user?')}</p>
        </Modal>
        <Modal title={t('users.Edit user')} open={this.state.isEditOpen} onOk={() => { this.handleEdit() }} onCancel={() => { this.setState({ isEditOpen: false }) }}>
          <div className='ul_form'>
            <div className='ul_form_title'>{t('users.firstName')}</div>
            <Input
              value={selectObj.firstName}
              status={selectObjError.firstName ? 'error' : ''}
              onChange={(e) => { this.editUser(e.target.value, 'firstName') }}
            />
            {selectObjError.firstName && <div className='ul_form_info'>{t('users.Please input the first name!')}</div>}
            <div className='ul_form_title'>{t('users.lastName')}</div>
            <Input
              value={selectObj.lastName}
              status={selectObjError.lastName ? 'error' : ''}
              onChange={(e) => { this.editUser(e.target.value, 'lastName') }}
            />
            {selectObjError.lastName && <div className='ul_form_info'>{t('users.Please input the last name!')}</div>}
            <div className='ul_form_title'>{t('users.email')}</div>
            <Input
              value={selectObj.email}
              status={selectObjError.email ? 'error' : ''}
              onChange={(e) => { this.editUser(e.target.value, 'email') }}
            />
            {selectObjError.email && <div className='ul_form_info'>{t('users.Please input the email!')}</div>}
            <div className='ul_form_title'>{t('users.role')}</div>
            <Select
              value={selectObj.role}
              status={selectObjError.role ? 'error' : ''}
              onChange={(e) => { this.editUser(e, 'role') }}
              allowClear
            >
              <Option value="administrator">{t('usersRole.Administrator')}</Option>
              <Option value="supervisor">{t('usersRole.Supervisor')}</Option>
              <Option value="user">{t('usersRole.User')}</Option>
            </Select>
            {selectObjError.role && <div className='ul_form_info'>{t('users.Please select the role!')}</div>}
          </div>
        </Modal>
        <Modal title={t('users.Change Password')} open={this.state.isChangePwdOpen} onOk={() => { this.handleChangePwd() }} onCancel={() => { this.setState({ isChangePwdOpen: false }) }}>
          <div className='ul_form'>
            <div className='ul_form_title'>{t('users.password')}</div>
            <Input
              value={pwdObj.password}
              status={pwdObjError.password ? 'error' : ''}
              onChange={(e) => { this.changePwd(e.target.value, 'password') }}
              type='password'
            />
            {pwdObjError.password && <div className='ul_form_info'>{t('users.Please input the password!')}</div>}
            <div className='ul_form_title'>{t('users.confirmPassword')}</div>
            <Input
              value={pwdObj.confirmPassword}
              status={pwdObjError.confirmPassword ? 'error' : ''}
              onChange={(e) => { this.changePwd(e.target.value, 'confirmPassword') }}
              type='password'
            />
            {pwdObjError.confirmPassword && <div className='ul_form_info'>{t('users.Please input the confirmPassword!')}</div>}
            {pwdObjError.confirmPasswordDiff && <div className='ul_form_info'>{t('users.The two passwords that you entered do not match!')}</div>}
          </div>
        </Modal>

      </div>
    )
  }
}


export default PageTable;